html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

body {
  background: rgb(0, 2, 63);
  background: linear-gradient(
    45deg,
    rgba(0, 2, 63, 1) 0%,
    rgba(27, 17, 66, 1) 24%,
    rgba(48, 25, 114, 1) 43%,
    rgba(68, 52, 100, 1) 61%,
    rgba(139, 50, 89, 1) 82%,
    rgba(204, 114, 54, 1) 100%
  );
  background-repeat: no-repeat;
}

.hero-container {
  position: relative;
  margin-top: 10%;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin: auto;
  /* border: 7px solid green; */
}

.hero-container::before {
  content: "";
  position: absolute;
  top: 500px;
  left: 0;
  width: 100%;
  height: 300px;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.4) 0%,
    rgba(0, 0, 0, 0.1) 46%,
    rgba(0, 0, 0, 0) 100%
  );
  background-repeat: no-repeat;
}

.hero-section {
  position: relative;
  margin: 0 50px 0 50px;
  display: flex;
  flex-wrap: wrap;
  width: auto;
  justify-content: center;
  border: 5px solid red;
}

.image-container {
  margin: 0;
  position: relative;
  width: auto;
  object-fit: cover;
  border: 3px solid purple;
}

img {
  width: 650px;
  height: auto;
}

.hero-content {
  margin-top: 65px;
  width: 42%;
  position: relative;
  text-align: left;
  justify-content: left;
  align-items: center;
  padding: 20px;
  /* border: 1px solid yellow; */
}

h1 {
  color: var(--pale-blue);
  font-weight: 600;
  font-size: 125px;
  line-height: 145px;
  margin-bottom: 20px;
  text-align: left;
}

h1 .thinner-heading {
  font-weight: 200;
}

.subhead-title {
  font-size: 28px;
  color: var(--pale-blue);
  font-weight: 300;
  margin-bottom: 65px;
  padding-left: 15px;
  line-height: 50px;
}

.hero-buttons {
  padding-left: 15px;
}

.portfolio-button {
  background: rgb(217, 67, 50);
  background: linear-gradient(
    0deg,
    rgba(217, 67, 50, 1) 9%,
    rgba(228, 87, 49, 1) 38%,
    rgba(234, 145, 77, 1) 73%,
    rgba(254, 198, 11, 1) 100%
  );
  padding: 10px 20px;
  border-radius: 5px;
  margin: 0 8px 0 0;
  font-size: 16px;
  font-weight: 400;
  color: var(--pale-blue);
  text-transform: uppercase;
  transition: all 0.3s;
}

.portfolio-button:hover,
.portfolio-button:focus {
  opacity: 50%;
}

.contact-button {
  border: 1px solid var(--pale-blue);
  padding: 9px 23px;
  border-radius: 5px;
  margin: 0 0 0 20px;
  font-size: 16px;
  font-weight: 300;
  color: var(--pale-blue);
  text-transform: uppercase;
  transition: 0.3s;
}

.contact-button:hover,
.contact-button:focus {
  opacity: 40%;
}

@media screen and (max-width: 2000px) {
  .hero-container {
    margin-top: 8%;
  }

  .hero-container::before {
    top: 735px;
  }
}

@media screen and (max-width: 1800px) {
  .hero-container {
    margin-top: 10%;
  }
}

@media screen and (max-width: 1200px) {
  .hero-container {
    margin-top: 12%;
  }

  .hero-container::before {
    top: 575px;
  }

  img {
    width: 510px;
  }

  .hero-content {
    margin-top: 15px;
  }

  h1 {
    font-size: 90px;
    line-height: 105px;
  }

  .subhead-title {
    padding-left: 15px;
    font-size: 20px;
    line-height: 45px;
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 992px) {
  .hero-container {
    margin-top: 17%;
  }

  .hero-container::before {
    top: 520px;
  }

  img {
    width: 460px;
  }

  .hero-content {
    width: 45%;
    padding-top: 20px;
    margin-top: 0;
  }

  h1 {
    font-size: 85px;
    line-height: 105px;
    margin-bottom: 18px;
  }

  .subhead-title {
    padding-left: 5;
    font-size: 17px;
    line-height: 40px;
    margin-bottom: 45px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container {
    margin-top: 15%;
  }

  .hero-container::before {
    top: 1160px;
  }

  img {
    width: 500px;
  }

  .hero-content {
    width: 100%;
    margin: auto;
    text-align: center;
    justify-content: center;
    margin-bottom: 45px;
  }

  h1 {
    font-size: 115px;
    line-height: 135px;
    text-align: center;
  }

  .subhead-title {
    padding-left: 0;
    font-size: 26px;
    line-height: 48px;
    margin-bottom: 55px;
  }
}

@media screen and (max-width: 576px) {
  .hero-section {
    width: 100%;
    margin: auto;
  }

  .hero-container::before {
    top: 1110px;
  }

  .hero-container {
    margin-top: 18%;
  }

  img {
    width: 480px;
  }

  h1 {
    font-size: 100px;
    line-height: 120px;
    text-align: center;
  }

  .subhead-title {
    padding-left: 0;
    font-size: 25px;
    line-height: 48px;
    margin-top: 20px;
    margin-bottom: 55px;
  }

  .hero-buttons {
    padding-left: 0;
  }
}

@media screen and (max-width: 430px) {
  .hero-container {
    margin-top: 26%;
  }

  .hero-container::before {
    top: 885px;
  }

  .hero-content {
    width: 100%;
    margin-bottom: 20px;
  }

  img {
    width: 360px;
  }

  h1 {
    font-size: 72px;
    line-height: 90px;
  }

  .subhead-title {
    font-size: 18px;
    line-height: 40px;
    margin-top: 22px;
    margin-bottom: 50px;
  }

  .hero-buttons {
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 390px) {
  .hero-container {
    margin-top: 22%;
  }

  .hero-container::before {
    top: 860px;
  }

  .hero-content {
    width: 100%;
    margin-bottom: 10px;
  }

  img {
    width: 330px;
  }

  h1 {
    font-size: 65px;
    line-height: 80px;
    text-align: center;
  }

  .subhead-title {
    padding-left: 0;
    font-size: 20px;
    line-height: 40px;
    letter-spacing: 0.04rem;
    margin-top: 20px;
    margin-bottom: 50px;
  }

  .hero-buttons {
    padding-left: 0;
  }
}
